import * as React from 'react';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import ListItemText from '@mui/material/ListItemText';
import ListItem from '@mui/material/ListItem';
import List from '@mui/material/List';
import Divider from '@mui/material/Divider';
import AppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import CloseIcon from '@mui/icons-material/Close';
import Slide from '@mui/material/Slide';
import DialogContentText from '@mui/material/DialogContentText';
import Paper from '@mui/material/Paper';



const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

export default function FullScreenDialog() {
  const [open, setOpen] = React.useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <div>
      <Button variant="outlined" className="full-description-btn" onClick={handleClickOpen}>        
        See Examples
      </Button>
      
      <Dialog
        fullScreen
        open={open}
        onClose={handleClose}
        TransitionComponent={Transition}
      >
        <AppBar sx={{ position: 'relative' }} className="app-bar">
          <Toolbar>
            <IconButton
              edge="start"
              color="inherit"
              onClick={handleClose}
              aria-label="close"
            >
              <CloseIcon />
            </IconButton>  
            <Typography sx={{ ml: 2, flex: 1, fontFamily: 'Poppins'}} variant="h5" component="div" >
              Full Instructions
            </Typography>
          </Toolbar>
        </AppBar>
        <List>
          <ListItem>
            <ListItemText disableTypography primary="Instructions" sx = {{fontSize: '150%'}}/>
          </ListItem>

          <DialogContentText sx={{ ml: 2, flex: 1, fontFamily: 'Poppins', padding: 3, textAlign: 'justify', lineHeight: 2, color: 'black', fontSize: 'small'}}  component="div">
            In this HIT, you will see a sentence with at least one ‘and’, please:
            <br/> 
            1. Split the sentence to multiple sentences by removing as many ‘and’ words as possible while preserving the sentence’s meaning, and assuring the sentences are as complete, readable, and grammatical as possible.
            <DialogContentText sx={{ ml: 2, flex: 1, fontFamily: 'Poppins', padding: 3, textAlign: 'justify', lineHeight: 2, color: 'black', fontSize: 'small', letterSpacing: 'normal'}}  component="div">
                ● When splitting, you are restricted to words that are in the original sentence 
            <br/>
                ● To keep grammar intact, change words to their fitting forms as needed (past/present/future, singular/plural, etc...) 
            <br/>
                ● It is okay if you remove other words than ‘and’, or use the same word more than one time
            <br/> 
            </DialogContentText>
            2. Make sure each sentence consists of at least one verb.
            <br/>
            <br/>
            3. Split sentences in the order you read them.
            <br/>
            <br/>
            4. If preserving the meaning of the sentence is impossible, submit it as is.
            <br/>
            <br/>
            NOTE: While the straightforward approach is to remove ‘and’ words, and split the sentences accordingly, notice that sometimes commas (‘,’) and other forms of punctuation should be removed too, as can be seen in the fifth Valid Example below. 
            <br/>
            <br/>
            TIP: A good rule of thumb is to add the removed ‘and’ word between the short sentences, and see if the meaning of the original sentence and the new long sentence is the same.
          </DialogContentText>
          <Divider />
        
          <ListItem>
            <ListItemText
              disableTypography
              primary="Valid Examples"
              sx = {{fontSize: '150%'}}
            />
            </ListItem>
                        
            <DialogContentText sx={{ ml: 2, flex: 1, fontFamily: 'Poppins', padding: 3, textAlign: 'justify', lineHeight: 2, color: 'black', fontSize: 'small'}}  component="div">
            <Typography sx = {{fontWeight: 'bold'}}>  1. James left unhappy today and unappreciated too. </Typography>
            ● James left unhappy today.
            <br/> 
            ● James left unappreciated today.
            <br/> 
            <Paper variant='outlined' sx={{gap: 2, bgcolor: 'lightGray', padding: '16px', marginTop: '10px', fontWeight: 'bold', fontSize: 'small'}}>
            'too' is replaced with 'today', as each sentence is standalone. 
            </Paper>
            </DialogContentText>

            <DialogContentText sx={{ ml: 2, flex: 1, fontFamily: 'Poppins', padding: 3, textAlign: 'justify', lineHeight: 2, color: 'black', fontSize: 'small'}}  component="div">
            <Typography sx={{fontWeight: 'bold'}}> 2. As of January 2013, The Times has a circulation of 399,339 and The Sunday Times of 885,612. </Typography>
            ● As of January 2013, The Times has a circulation of 399,339. 
            <br/> 
            ● As of January 2013, The Sunday Times has a circulation of 885,612. 
            </DialogContentText>

            <DialogContentText sx={{ ml: 2, flex: 1, fontFamily: 'Poppins', padding: 3, textAlign: 'justify', lineHeight: 2, color: 'black', fontSize: 'small'}}  component="div">
            <Typography sx={{fontWeight: 'bold'}}> 3. According to the Merton Thesis, there was a positive correlation between the rise of English Puritanism and German Pietism on the one hand and early experimental science on the other. </Typography>
            ● According to the Merton Thesis, there was a positive correlation between the rise of English Puritanism and German Pietism.
            <br/>
            ● According to the Merton Thesis, there was a positive correlation between the rise of English Puritanism and early experimental science.
          </DialogContentText>

          <DialogContentText sx={{ ml: 2, flex: 1, fontFamily: 'Poppins', padding: 3, textAlign: 'justify', lineHeight: 2, color: 'black', fontSize: 'small'}}  component="div">
          <Typography sx={{fontWeight: 'bold'}}> 4. Donnie is eating pizza and Dan used to, too. </Typography>
            ● Donnie is eating pizza.
            <br/>
            ● Dan used to eat pizza too.
          </DialogContentText>

          <DialogContentText sx={{ ml: 2, flex: 1, fontFamily: 'Poppins', padding: 3, textAlign: 'justify', lineHeight: 2, color: 'black', fontSize: 'small'}}  component="div">
          <Typography sx={{fontWeight: 'bold'}}> 5. Genetic studies have found significant African female-mediated gene flow in Arab communities in the Arabian Peninsula and neighboring countries, with an average of 38% of maternal lineages in Yemen are of direct African descent, 16% in Oman-Qatar, and 10% in Saudi Arabia-United Arab Emirates. </Typography>
            ● Genetic studies have found significant African female-mediated gene flow in Arab communities in the Arabian Peninsula and neighboring countries, with an average of 38% of maternal lineages in Yemen are of direct African descent.
            <br/>
            ● Genetic studies have found significant African female-mediated gene flow in Arab communities in the Arabian Peninsula and neighboring countries, with an average of 16% in Oman-Qatar.
            <br/>
            ● Genetic studies have found significant African female-mediated gene flow in Arab communities in the Arabian Peninsula and neighboring countries, with an average of 10% in Saudi Arabia-United Arab Emirates.
          </DialogContentText>
            <Divider />
           
            <ListItem>
            <ListItemText
              disableTypography
              primary="Invalid Examples"
              sx = {{fontSize: '150%'}}
            />
          </ListItem>
          <DialogContentText sx={{ ml: 2, flex: 1, fontFamily: 'Poppins', padding: 3, textAlign: 'justify', lineHeight: 2, color: 'black', fontSize: 'small'}}  component="div">
          <Typography sx={{fontWeight: 'bold'}}> 1. In 2013, videos of the 2.5% of artists categorized as "mega", "mainstream" and "mid-sized" received 90.3% of the relevant views on YouTube and Vevo. </Typography>
            ● In 2013, videos of the 2.5% of artists categorized as "mega" received 90.3% of the relevant views on YouTube and Vevo.
            <br/>
            ● In 2013, videos of the 2.5% of artists categorized as "mainstream" received 90.3% of the relevant views on YouTube and Vevo.
            <br/>
            ● In 2013, videos of the 2.5% of artists categorized as "mid-sized" received 90.3% of the relevant views on YouTube and Vevo. 
            <Paper variant='outlined' sx = {{gap: 2, bgcolor: 'lightGray', padding: '16px', marginTop: '10px', fontWeight: 'bold', fontSize: 'small'}}>
            The video categories combined create 90.3% of the relevant views on YouTube and Vevo. But we do not know what are the relevant views for each category, so we cannot split them.
            </Paper>
          </DialogContentText>

          <DialogContentText sx={{ ml: 2, flex: 1, fontFamily: 'Poppins', padding: 3, textAlign: 'justify', lineHeight: 2, color: 'black', fontSize: 'small'}}  component="div">
            <Typography sx={{fontWeight: 'bold'}}> 2.  According to the Merton Thesis, there was a positive correlation between the rise of English Puritanism and German Pietism on the one hand and early experimental science on the other.</Typography>
            <Paper variant='outlined' sx = {{gap: 2, bgcolor: 'lightGray', padding: '16px', marginTop: '10px', fontWeight: 'bold', fontSize: 'small'}}>
            'English Puritanism' and 'German Pietism' are connected by the word 'between', and there is no way to remove the first ‘and’ word without changing the meaning of the sentence
            </Paper>
          </DialogContentText>

        </List>
      </Dialog>
    </div>
  );
}
