import { FaTimes } from 'react-icons/fa'
const Sentence = ({ sentence, onDelete }) => {
    return (
        <div className={sentence}>
            <h3>
                {sentence.text} <FaTimes style={{ 
                color: 'red', 
                cursor: 'pointer' }}
                onClick={() => onDelete(sentence.id)}
                /> 
            </h3>
        </div>
    )
}

export default Sentence 
