 import { useState } from 'react'
 import TextField from '@mui/material/TextField';

const AddSentence = ( {prompt, allowedWords, sentences, onAdd} ) => {
    let [text, setText] = useState('')

    const onSubmit = (e) => {
        e.preventDefault()
        
        if (!text) {
            alert('There is no text to save')
            return
        }
        allowedWords.push('a') // for singular forms

        let modifiedText = text.trim().toLowerCase()
        let modifiedTextAsArray = modifiedText.split(" ")
        console.log(allowedWords)
        console.log(modifiedTextAsArray)

        const notAllowedInSentence = modifiedTextAsArray.some(w => !allowedWords.includes(w)) // not even one word is in notAllowed
        if(notAllowedInSentence) {
            alert("A word or an extra backspace between words that is not in the prompt was used")
            return
        }
        
        const isSentenceIncluded = sentences.map(s => s.text.toLowerCase()).includes(modifiedText)
        if(isSentenceIncluded) {
            alert("The exact sentence is already saved")
            return
        }

        onAdd({text})
        setText('')
    }
    return (
        <form className='add-form' onSubmit={onSubmit}>
            <div className='form-control'>
                <label>Sentence</label>
                <input type='text' placeholder='Add Sentence' value={text} onChange={(e) => setText(e.target.value)}/>
                {/* <TextField id="outlined-basic" label="Write Sentence..." variant="outlined" fullWidth /> */}
            </div>

            <input type='submit' value='Save Sentence' className='btn btn-block save-sentence' />
        </form>
        
    )
}

export default AddSentence
