import { useState } from 'react'
import Header from './components/Header'
import Prompt from './components/Prompt'
import Sentences from './components/Sentences'
import AddSentence from './components/AddSentence'
import Submit from './components/Submit'

function App({prompt, wordForms}) {
  const [showAddSentence, setShowAddSentence] = useState(true)
  const [sentences, setSentences] = useState([])


const addSentence = (sentence) => {
  const id = Math.floor(Math.random() * 10000) + 1
  const newSentence = {id, ...sentence }
  setSentences([...sentences, newSentence])
}

const deleteSentence = (id) => {
  setSentences(sentences.filter((sentence) => sentence.id != id))
}

  return (
    <div className='container'>
      <Header/>
      <Prompt prompt={prompt} onAdd={() => setShowAddSentence(!showAddSentence)} showAdd={showAddSentence}/>
      
      {showAddSentence && <AddSentence prompt={prompt} allowedWords={wordForms} sentences={sentences} onAdd={addSentence} />}
      {sentences.length ? (<Sentences sentences={sentences} onDelete={deleteSentence} />) : ''}
      <Submit sentences={sentences}/>
    </div>
  )
}

export default App;
