import { useState } from 'react'

const Submit = ({ sentences }) => {
    const [feedback, setFeedback] = useState('')
    sentences = sentences.map(s => '<s> ' + s.text + ' </s>')
    const onSubmit = (e) => {
        e.preventDefault()
        if (sentences.length == 0) {
            alert('There are no sentences to submit')
            return
        }

        document.querySelector('crowd-form').onsubmit = () => {
            document.getElementById('data-sentences').value = sentences
            document.getElementById('data-feedback').value = feedback
        };
        document.querySelector('crowd-form').submit();

        setFeedback('')
    }
    return (
        <form className='add-form submit-footer' onSubmit={onSubmit}>
            <div className='submit-form-control'>
                <label>Having Trouble? </label>
                <input type='text' placeholder='Add Feedback' value={feedback} onChange={(e) => setFeedback(e.target.value)} />
            </div>

            <input type='submit' value='Submit' className='btn btn-block-submit' />
        </form>
    )
}

export default Submit
