import PropTypes from 'prop-types'
import InstructionsWindow from './InstructionsWindow'
import DialogContentText from '@mui/material/DialogContentText';


const Header = ({ title }) => {
    return (
        <header className='header'>
            <h1>{title}</h1>
            <br />
            <br/> 
            1. Split the sentence to multiple sentences by removing as many ‘and’ words as possible while preserving the sentence’s meaning, and assuring the sentences are as complete, readable, and grammatical as possible.
            <DialogContentText 
            sx={{ ml: 2, flex: 1, fontFamily: 'Poppins', padding: 3, textAlign: 'justify', lineHeight: 2, color: 'black', 
            fontSize: 'small', letterSpacing: 'normal'}}  component="header">
                ● When splitting, you are restricted to words that are in the original sentence 
            <br/>
                ● To keep grammar intact, change words to their fitting forms as needed (past/present/future, singular/plural, etc...) 
            <br/>
                ● It is okay if you remove other words than ‘and’, or use the same word more than one time
            <br/> 
            </DialogContentText>
            2. Make sure each sentence consists of at least one verb.
            <br/>
            <br/>
            3. Split sentences in the order you read them.
            <br/>
            <br/>
            4. If preserving the meaning of the sentence is impossible, submit it as is.
            <br/>
            <br/>
            NOTE: While the straightforward approach is to remove ‘and’ words, and split the sentences accordingly, notice that sometimes commas (‘,’) and other forms of punctuation should be removed too, as can be seen in the fifth Valid Example. 
            <br/>
            <br/>
            TIP: A good rule of thumb is to add the removed ‘and’ word between the short sentences, and see if the meaning of the original sentence and the new long sentence is the same.
            <br/>
            <br/>
            <InstructionsWindow/>

        </header>
    )
}

Header.defaultProps = {
    title: 'Sentence Segmentation',
}
Header.propTypes = {
    title: PropTypes.string.isRequired,
}


export default Header

