import PropTypes from 'prop-types'
import Button from './Button'
import Typography from '@mui/material/Typography';

const reactStringReplace = require('react-string-replace');

const Prompt = ({prompt, onAdd, showAdd}) => {    
    const coloredPrompt = reactStringReplace(prompt, /\b(and)\b/g, (match, i) => (
        <span key={i} style={{ color: '#9900ff' }}>{match}</span>
      ))
 

    return (
        <div className='prompt'>
            <Typography variant="h6" component="h6" sx =  {{fontFamily: 'Times New Roman; Times; Ariel', fontStyle: 'italic'}}>
                {coloredPrompt}
            </Typography>
            {/* <Button color={showAdd ? 'red' : 'green'} text={showAdd ? 'Close' : 'Add'} onClick={onAdd}/> */}
        </div>
    )
}

export default Prompt
